</div>
                                                <p>基本用法</p>
<pre v-pre=""><code class="hljs language-html">    <span class="hljs-tag">&lt;<span class="hljs-name">fm-radio-button-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"val"</span> <span class="hljs-attr">:data-source</span>=<span class="hljs-string">"groups"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">fm-radio-button-group</span>&gt;</span> 
    <span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
        <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
            data () {
                <span class="hljs-keyword">return</span> {
                    <span class="hljs-attr">val</span>: <span class="hljs-string">'哈哈'</span>,
                    <span class="hljs-attr">groups</span>: [<span class="hljs-string">'全部'</span>, <span class="hljs-string">'历史跟随'</span>, <span class="hljs-string">'正在跟随'</span>, <span class="hljs-string">'哈哈'</span>]
